	module.exports = {"data":{"markdownRemark":{"html":"<p><a href=\"/dynabook-sslx190\">dynabookSS LX</a>が2008年頃からバッテリが劣化してしまい、以降は専らデスクトップ専用機として活躍していましたが(それも2010年冬に逝去)、転職の都合上再び電車通勤となり、この時間を有効に活用したいと思いモバイル用に急遽購入。その機動性とバッテリの持ちの長さは、<a href=\"/cf-w2d\">Let'sNote W2</a>に通じるものがある。久々の機動性と電車酔いに酔いしれて通勤しています。 ちょっと性能が弱いが(CPUだけなら<a href=\"/netvista-m42slim\">NetVista M42Slim</a>並み)、この程度が丁度良い。ゲームの低性能PCでの検証にも役立つし。 ノート後継の Surface Pro 購入後はしばらく低スペック動作試験や自宅サイネージの実験とかやっていたけど、最終的にヤフオクで処分。</p>\n<h2>製作物・他やってたこと</h2>\n<ul>\n<li>\n<p><a href=\"http://nue.sourceforge.jp/\">Network Utterance Environment</a></p>\n<h2>性能</h2>\n</li>\n<li>\n<p>CPU:intel Atom N450(Pineview/1.66GHz 1C2T)</p>\n</li>\n<li>\n<p>RAM:2GB</p>\n</li>\n<li>\n<p>VGA:intel GMA3150</p>\n</li>\n<li>\n<p>HDD:320GB</p>\n</li>\n<li>\n<p>CDD:(NONE)</p>\n</li>\n<li>\n<p>NET:IEEE802.11b&#x26;g</p>\n</li>\n<li>\n<p>SOUND:HD Audio</p>\n</li>\n<li>\n<p>OS:Windows8.1 Pro / Fedora19</p>\n<h2>Windows Experience Index</h2>\n</li>\n<li>\n<p>プロセッサ : <em>2.3</em></p>\n</li>\n<li>\n<p>メモリ (RAM) : 4.6</p>\n</li>\n<li>\n<p>グラフィックス : 3.2</p>\n</li>\n<li>\n<p>ゲーム用グラフィックス : 3.0</p>\n</li>\n<li>\n<p>プライマリ ハード ディスク : 5.7</p>\n</li>\n</ul>","frontmatter":{"cover":null,"date":"2011-05-01T21:55:00+09:00","strDate":"2011/5/1","title":"(2011-2015) ASUS EeePC 1008P-KR"}}},"pathContext":{"path":"/ja/blog/2011/05/01-eeepc-1008p-kr/","langKey":"ja"}}

